import React from "react";
// Homepage Demo
import { HashRouter, Route, Routes } from "react-router-dom";
// Foody document
import FoodyDoc from "../views/foodyman-doc";
import FoodyIntroduction from "../views/foodyman-doc/Introduction";
import FoodyBasic from "../views/foodyman-doc/Basic";
import FoodyServer from "../views/foodyman-doc/Server";
import FoodyMobileApp from "../views/foodyman-doc/Mobile-app";
import FoodyFlutterSDK from "../views/foodyman-doc/Flutter-SDK";
import FoodyLocalServer from "../views/foodyman-doc/Local-server";
import FoodyInstallOnServer from "../views/foodyman-doc/Install-on-server";
import FoodyMandatorySetup from "../views/foodyman-doc/Mandatory-setup";
import FoodyCustomization from "../views/foodyman-doc/Customization";
import FoodyMandatorySetupMobile from "../views/foodyman-doc/Mandatory-setup-mobile";
import FoodyMandatorySetupDeliveryboy from "../views/foodyman-doc/Moderator-setup-deliveryboy";
import FoodyMandatorySetupVendor from "../views/foodyman-doc/Moderator-setup-vendor";
import FoodyMandatorySetupCustomer from "../views/foodyman-doc/Moderator-setup-customer";
import FoodyCustomizationMobile from "../views/foodyman-doc/Customization-mobile";
import FoodyMandatorySetupWeb from "../views/foodyman-doc/Mandatory-setup-web";
import FoodyMandatorySetupBackend from "../views/foodyman-doc/Mandatory-setup-backend";
import FoodyBuildCodeAndSetupOnServer from "../views/foodyman-doc/Build-code-and-setup-on-server";
import FoodyBuildCodeAndSetupOnServerBackend from "../views/foodyman-doc/Build-code-and-setup-on-server-backend";
import FoodyUpdateAdminPanel from "../views/foodyman-doc/Update-admin-panel";
import FoodyUpdateAppWeb from "../views/foodyman-doc/Update-app-web";
import FoodyFront from "../views/foodyman-doc/front";
import FoodyLocalFront from "../views/foodyman-doc/local-front";
import FoodySupportPlan from "../views/foodyman-doc/supportPlan";
import FoodyFirebaseSetup from "../views/foodyman-doc/firebase-setup";
import TroubleshootingBackend from "../views/foodyman-doc/Troubleshooting-backend";
import CustomizationMobileCustomer from "../views/foodyman-doc/Customization-mobile-customer";
import CustomizationMobileDelivery from "../views/foodyman-doc/Customization-mobile-delivery";
import TroubleshootingAdmin from "../views/foodyman-doc/Troubleshooting-admin";

//  app builde
import AppBuildReleaseCustomer from "../views/foodyman-doc/App-build-release-customer";
import AppBuildReleaseDelivery from "../views/foodyman-doc/App-build-release-deliveryboy";
import AppBuildReleaseVendor from "../views/foodyman-doc/App-build-release-vendor";

const Routing = () => {
  return (
    <>
      <HashRouter>
        <Routes>
          {/* foodyman documentation */}
          <Route path="/" element={<FoodyDoc />}>
            <Route path="/" element={<FoodyIntroduction />} />
            <Route
              path="/foodyman-documentation/basic"
              element={<FoodyBasic />}
            />
            <Route
              path="/foodyman-documentation/server"
              element={<FoodyServer />}
            />
            <Route
              path="/foodyman-documentation/front"
              element={<FoodyFront />}
            />
            <Route
              path="/foodyman-documentation/local-front"
              element={<FoodyLocalFront />}
            />
            <Route
              path="/foodyman-documentation/mobile-app"
              element={<FoodyMobileApp />}
            />
            <Route
              path="/foodyman-documentation/flutter-sdk"
              element={<FoodyFlutterSDK />}
            />
            <Route
              path="/foodyman-documentation/local-server"
              element={<FoodyLocalServer />}
            />
            <Route
              path="/foodyman-documentation/install-on-server"
              element={<FoodyInstallOnServer />}
            />
            <Route
              path="/foodyman-documentation/mandatory-setup"
              element={<FoodyMandatorySetup />}
            />
            <Route
              path="/foodyman-documentation/customization"
              element={<FoodyCustomization />}
            />
            <Route
              path="/foodyman-documentation/mandatory-setup-mobile"
              element={<FoodyMandatorySetupMobile />}
            />
            <Route
              path="/foodyman-documentation/mandatory-setup-backend"
              element={<FoodyMandatorySetupBackend />}
            />
            <Route
              path="/foodyman-documentation/mandatory-setup-vendor"
              element={<FoodyMandatorySetupVendor />}
            />
            <Route
              path="/foodyman-documentation/mandatory-setup-customer"
              element={<FoodyMandatorySetupCustomer />}
            />
            <Route
              path="/foodyman-documentation/mandatory-setup-deliveryboy"
              element={<FoodyMandatorySetupDeliveryboy />}
            />
            <Route
              path="/foodyman-documentation/customization-vendor"
              element={<FoodyCustomizationMobile />}
            />
            <Route
              path="/foodyman-documentation/customization-deliveryboy"
              element={<CustomizationMobileDelivery />}
            />
            <Route
              path="/foodyman-documentation/customization-customer"
              element={<CustomizationMobileCustomer />}
            />
            <Route
              path="/foodyman-documentation/customer-app-build-release"
              element={<AppBuildReleaseCustomer />}
            />
            <Route
              path="/foodyman-documentation/vendor-app-build-release"
              element={<AppBuildReleaseVendor />}
            />
            <Route
              path="/foodyman-documentation/deliveryboy-app-build-release"
              element={<AppBuildReleaseDelivery />}
            />
            <Route
              path="/foodyman-documentation/mandatory-setup-web"
              element={<FoodyMandatorySetupWeb />}
            />
            <Route
              path="/foodyman-documentation/build-code-and-setup-on-server"
              element={<FoodyBuildCodeAndSetupOnServer />}
            />
            <Route
              path="/foodyman-documentation/build-code-and-setup-on-server-backend"
              element={<FoodyBuildCodeAndSetupOnServerBackend />}
            />
            <Route
              path="/foodyman-documentation/update-admin-panel"
              element={<FoodyUpdateAdminPanel />}
            />
            <Route
              path="/foodyman-documentation/update-app-web"
              element={<FoodyUpdateAppWeb />}
            />
            <Route
              path="/foodyman-documentation/support-plan"
              element={<FoodySupportPlan />}
            />
            <Route
              path="/foodyman-documentation/firebase"
              element={<FoodyFirebaseSetup />}
            />
            <Route
              path="/foodyman-documentation/troubleshooting-backend"
              element={<TroubleshootingBackend />}
            />
            <Route
              path="/foodyman-documentation/troubleshooting-admin"
              element={<TroubleshootingAdmin />}
            />
          </Route>
        </Routes>
      </HashRouter>
    </>
  );
};

export default Routing;
